* {
  --webkit-tab-hightlight-color: rgba(#000, 0);
  --webkit-tab-hightlight-color: transparent; }

html {
  height: 100%;
  font-size: 1em; }

body {
  font-size: 16px;
  line-height: 24px;
  background-color: white;
  min-height: 100vh;
  min-width: 320px;
  font-family: 'Fira Sans', 'Helvetica', sans-serif;
  font-weight: 400; }

p {
  margin-top: 0;
  margin-bottom: 24px; }

b,
strong {
  font-weight: 700; }

ul,
ol {
  padding: 0;
  margin: 0; }

ul {
  list-style: none; }

li {
  padding: 0; }

a {
  color: inherit;
  outline: none; }

h6,
h5,
h4,
h3,
h2,
h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: inherit;
  box-sizing: border-box; }

h6 {
  font-size: 18px;
  line-height: 1.333; }

h5 {
  font-size: 20px;
  line-height: 1.2; }

h4 {
  font-size: 22px;
  line-height: 1.091; }

h3 {
  font-size: 24px;
  line-height: 1; }

h2 {
  font-size: 26px;
  line-height: 1.848; }

h1 {
  font-size: 28px;
  line-height: 1.718; }

small {
  display: block;
  font-size: 14px;
  line-height: 1.714;
  margin-bottom: 0; }

::selection {
  background-color: #c6c7c8;
  color: black; }

.hidden,
[hidden] {
  display: none !important;
  visibility: hidden !important; }

.hidden-visibility {
  visibility: hidden;
  opacity: 0; }

.breakline {
  display: block; }

.link {
  cursor: pointer;
  text-decoration: underline; }
  .link:disabled, .link[disabled] {
    cursor: not-allowed; }

.align-middle {
  display: inline-block;
  height: 100%;
  vertical-align: middle; }

.text-center {
  text-align: center; }

.overflow-hidden {
  overflow: hidden; }

.btn {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: 2px;
  padding: 8px 40px;
  font-family: 'Fira Sans', 'Helvetica', sans-serif;
  font-weight: 300;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  box-sizing: border-box; }
  .btn::before {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 0;
    background-color: white;
    background-color: rgba(255, 255, 255, 0.2);
    transition: all 400ms ease-in-out;
    content: "";
    clear: both;
    overflow: hidden; }
  .btn:hover::before {
    width: 100%; }

.btn-outline {
  border: 1px solid white;
  background-color: transparent;
  color: white; }

.hero {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 60px;
  height: 100%;
  min-height: 100vh;
  background-color: #26b1dc;
  background: linear-gradient(90deg, #26b1dc, #6d2589);
  box-sizing: border-box;
  z-index: 0; }
  @media only screen and (max-width: 480px) {
    .hero {
      padding-left: 0; } }
  .hero-box {
    max-width: 960px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    animation: fadeup 600ms cubic-bezier(0.86, 0, 0.07, 1) forwards; }
  .hero-title {
    margin-bottom: 16px;
    color: white;
    font-family: 'Fira Sans', 'Helvetica', sans-serif;
    font-weight: 700;
    font-size: 120px;
    line-height: 1;
    text-align: center;
    text-transform: uppercase; }
    @media only screen and (max-width: 960px) {
      .hero-title {
        font-size: 80px; } }
    @media only screen and (max-width: 768px) {
      .hero-title {
        font-size: 40px; } }
  .hero-description {
    position: relative;
    margin-right: auto;
    margin-left: auto;
    width: 100%;
    max-width: 600px;
    color: white;
    text-align: center;
    box-sizing: border-box; }
    @media only screen and (max-width: 768px) {
      .hero-description {
        padding: 0 40px;
        max-width: 420px; } }
    .hero-description p {
      margin-top: 0;
      font-size: 18px;
      letter-spacing: .2em; }
      @media only screen and (max-width: 960px) {
        .hero-description p {
          font-size: 16px; } }
      @media only screen and (max-width: 768px) {
        .hero-description p {
          font-size: 14px; } }
  .hero-actions {
    text-align: center; }

@keyframes fadeup {
  from {
    opacity: .2;
    transform: translateY(50%); }
  to {
    opacity: 1;
    transform: translateY(0); } }

.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  width: 60px;
  z-index: 10;
  background-color: white; }
  @media only screen and (max-width: 480px) {
    .sidebar {
      right: 0;
      bottom: auto;
      width: 100%;
      height: 40px; } }
  .sidebar-box {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    font-size: 24px;
    box-sizing: border-box; }
    @media only screen and (max-width: 480px) {
      .sidebar-box {
        padding: 0 20px;
        flex-direction: row;
        font-size: 18px; } }
  .sidebar-nav {
    text-align: center;
    color: #26b1dc;
    margin-bottom: 32px; }
    @media only screen and (max-width: 480px) {
      .sidebar-nav {
        margin-bottom: 0;
        margin-right: 20px; } }
  .sidebar-search {
    text-align: center;
    color: #26b1dc;
    margin-bottom: 32px; }
    @media only screen and (max-width: 480px) {
      .sidebar-search {
        margin-bottom: 0;
        margin-right: 20px; } }
  .sidebar-share {
    margin-top: auto;
    margin-bottom: 0; }
    @media only screen and (max-width: 480px) {
      .sidebar-share {
        display: flex;
        margin-left: auto;
        margin-right: 0;
        margin-top: auto;
        margin-bottom: auto; } }
  .sidebar .share-item {
    display: block;
    margin-bottom: 32px;
    text-align: center;
    color: #6d2589; }
    @media only screen and (max-width: 480px) {
      .sidebar .share-item {
        margin-bottom: 0;
        margin-left: 20px; } }
